@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
body {
    overflow-x: hidden;
    position: relative;
    font-family: 'Montserrat';
    cursor: url('/public/assets/mcursor.png'), auto;
}

.bgImage {
    height: 100vh;
}

.header {
    z-index: 999;
    margin-top: 0;
    display: flex;
    width: 100vw;
    position: fixed;
    padding: 2vh 4vw;
}

@font-face {
    font-family: logofont;
    src: url(../fonts/logofont.ttf);
}

.logoWrapper {
    font-family: logofont;
    display: flex;
    align-items: center;
}

.logoImg, .logoTxt {
    display: flex;
    flex-direction: column;
}

.logoPhoto {
    height: 90px;
    transition: 0.3s;

}

.logoTxt {
    font-size: 50px;
    color: white;
}

.navbar {
    padding: 16px;
    width: 65%;
    margin-left: auto;

}

.navbar ul {
    list-style: none;
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.navbar ul li {
    transition: 0.3s;
    font-size: 18px;
    color: white;
}

.navbar ul li:nth-child(6) {
    border-right: 2px white solid;
    padding-right: 16px;
}

.navbar ul li a {
    text-decoration: none;
    color: white;
    font-size: 1.5vw;

}

.fphoto {
    margin-top: 0;
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100vh;
    background: url('/public/assets/Background_Clean.png');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.about {
    padding: 8% 0;
    padding-bottom: 0%;
    background-color: white;
    color: black;
    font-weight: 400;
}

.about .section:nth-child(1) {
    display: flex;
    padding: 32px 8%;
}
.about .section:nth-child(1) .text{
    width: 160%;
}
.about .section:nth-child(1) .text p{
    font-size: 20px;
}

.about .section:nth-child(2) {
    position: relative;
    padding-top: 32px;
    font-family: 'Montserrat';

}

.about .section:nth-child(2) video {
    width: 100%;
    height: auto;
    z-index: 3;
}

.about .video {
    z-index: 0 !important;
}


.about .section:nth-child(2) .text {
    padding: 32px 0;
    width: 90%;
    margin: auto;
    /* position: absolute;
    top: 0;
    left: 0;
    z-index: 4 !important;*/
}

.about .section:nth-child(2) .text h2{
    text-align: center;
    margin: 16px 0;
    font-weight: 400;
    font-size: 32px;
}
.about .section:nth-child(2) p{
    font-size: 20px;
    margin: auto;
}
.about .ul{
    font-size: 20px;
    padding: 4% 8%;
    background: url('/public/assets/aboutnf5.png');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}
.about p {
    font-size: 1vw;
}

.img img {
    width: 100%;
}

.text {
    margin-left: 5%;
}

.text h2 {
    font-weight: 500;
    font-size: 32px;
}

#header {
    transition: 0.3s;
}

#header.sticky {
    background: #fff;
    transition: 0.3s;
}

#header.sticky .logoTxt, #header.sticky .navbar li a {
    color: #000 !important;
}

#header.sticky .navbar ul li:nth-child(6) {
    border-right: 2px #000 solid;
    padding-right: 16px;
}

#header.sticky .toggle:before {
    color: black;
}

.toggle {
    display: none;
    transition: 0.3s;

}

@media only screen and (max-width: 800px) {
    .logoTxt {
        display: none;
    }

    .navbaritems {
        display: none !important;
    }

    .navbaritems.active {
        display: block !important;
    }

    .toggle {
        display: block;
        margin-left: auto;
        margin-top: auto;
        margin-bottom: auto;
    }

    .toggle.active {
        margin-right: auto;
        margin-left: auto;
        transition: 0.3s;

    }

    .toggle:before {
        content: '\f0c9';
        font-family: fontAwesome;
        line-height: 40px;
        color: white;
        font-size: 32px;
    }

    .toggle.active:before {
        content: '\f00d';
        font-family: fontAwesome;
        line-height: 40px;
        color: white;
        font-size: 32px;
    }

    .navbaritems.active li {
        margin-top: 16px;
        transition: 0.3s;

    }

    .logoPhoto.active {
        height: 128px;
        transition: 0.3s;
    }

    #header .navbar ul li:nth-child(6) {
        border: none !important;
    }
}

.floatingplane {
    width: 720px;
    position: absolute;
    top: 200px;
}



.team {
    background: url('../assets/bg2.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    backdrop-filter: blur(5px);
    color: white;
    text-align: center;
    padding: 64px 0;
}

.slideritem {
    width: 100%;
    height: 100%;
    padding: 16px 10%;
    text-align: left;
}

.sliderimage img {
    width: 100%;

}








.faq {
    margin-top: 32px;
    text-align: center;
}

.faq .quests {
    text-align: left;
}

.faq .quests {
    background-color: #e6e6e6;
    margin-top: 64px;
    padding: 16px 32px;
    padding-bottom: 260px;

}

.faq .quests .quest h2 {
    font-size: 24px;
}

.faq .quests .quest {
    margin-top: 32px;
    border-bottom: 1px red solid;
    width: 40%;
}

.faq .quests .quest:nth-child(1), .faq .quests .quest:nth-child(3) {
    text-align: right;
    margin-left: auto;
}

.media {
    text-align: center;
    font-size: 64px;
    color: white;
    overflow: hidden;
}

.media h1 {
    font-size: 64px;
}

.media .flyinplane {
    height: 256px;
    position: absolute;
    left: 0px;
    z-index: 3;
}

.medcontent {
    padding: 32px;
}

.media a:hover>i {
    color: red;
    transition: 0.3s;
}

.media i {
    transition: 0.3s;
}

.media .boxes {
    z-index: 4;
    padding-top: 384px;
    display: flex;
    width: 60%;
    justify-content: space-between;
    margin: auto;
}

.media .boxes .box a {
    color: white;
    text-decoration: none;
}

.media {
    transform: skew(0deg, -3deg) translateY(-200px);
}

.medbg {
    padding: 64px 16px;
    background: url("../assets/bg4.jpg");
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    height: 1000px;
    transform: skew(0deg, 3deg) translateY(-80px);

}

.contact {
    padding: 32px 32px;
    background: url('/public/assets/bg5.jpg');

}

.contact h1 {
    z-index: 4;
    font-size: 32px;
    margin-bottom: 32px;
    text-align: center;
}

.contact h3 {
    font-size: 24px;
}

.contactform {
    padding: 32px 32px;
    width: 60%;
    margin: auto;
    border: 1px red solid;
    text-align: center;
}

.contactform .row {
    margin-bottom: 16px;
}

.contactform input, .contactform textarea {
    width: 100%;
    padding: 8px;
}

.contactform button {
    width: 20%;
    margin: auto;
    padding: 8px;
    color: white;
    background: red;
    border: none;
    outline: none;
}

.apply {
    padding: 5% 5%;
    text-align: center;
    background: url('/public/assets/apply.png');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    color: white;
}

.numberindications {
    width: 80%;
    display: flex;
    margin: auto;
}

.indicator {
    width: auto;
    margin: auto;
    height: 128px;
    overflow: hidden;
}

.indicator:hover {
    border-bottom: 4px red solid;
}

.indicator img {
    width: 128px;
    height: 256px;
    object-fit: cover;
}

.indicator:nth-child(2) img {
    width: 128px;
    height: 256px;
    object-fit: cover;
    object-position: 80% 100%;
    transform: translateY(-40px);
}

.indicator:nth-child(3) img {
    width: 128px;
    height: 256px;
    object-fit: cover;
    object-position: 16% 100%;
    transform: translateY(-40px);

}

.indicator:nth-child(4) img {
    width: 128px;
    height: 256px;
    object-fit: cover;
    object-position: 50% 0%;
    transform: translateY(-93px);

}

.indicator:nth-child(5) img {
    width: 128px;
    height: 256px;
    object-fit: cover;
    object-position: -12% 0%;
    transform: translateY(-83px);

}

.indicator:nth-child(6) img {
    width: 128px;
    height: 256px;
    object-fit: cover;
    object-position: 112% 0%;
    transform: translateY(-83px);

}

.indicator:nth-child(7) img {
    width: 128px;
    height: 256px;
    object-fit: cover;
    object-position: 16% 0%;
    transform: translateY(-132px);

}

.indicator:nth-child(8) img {
    width: 128px;
    height: 256px;
    object-fit: cover;
    object-position: 80% 0%;
    transform: translateY(-132px);

}



.team .details {
    width: 80%;
    margin: auto;
    display: flex;
    margin-top: 64px;
    padding: 32px;
    background-color: rgba(0, 0, 0, 0.5);

}

.team .details img {
    width: 320px;
    height: 320px;
    object-fit: cover;
}

.team .details .text {
    text-align: left;
}

.team .crew{
    margin-top: 64px;
}
.team .crew .row{
    width: 80%;
    margin: auto;
}
.team .crew .col-lg-4{
    padding: 16px;
}
.team .crew .col-lg-4 .cards{
    background-color: rgba(0, 0, 0, 0.5) !important;
    padding: 4px;
    height: 550px;

}
.team .crew .col-lg-4 .cards h3{
    font-weight: 300;
}
.team .crew .col-lg-4 .cards h2{
    font-weight: 400;
}
@media only screen and (max-width: 800px) {

    .floatingplane {
        width: 320px;
        top: 200px;
    }

    .navbar ul li a {
        font-size: 20px;
    }

    .about .section:nth-child(1){
        display: block;
    }
    .about .section:nth-child(2) .text{
        position: relative;
        color: black;
    }
    .about p {
        font-size: 20px;
    }

    .faq .quests .quest {
        width: 80%;
    }

    .media .boxes {
        width: 90%;
    }

    .contactform {
        width: 100%;
    }

    .contactform input {
        margin-top: 32px;
    }

    .contactform button {
        width: 50%;
    }

    .numberindications {
        display: block !important;
    }

    .details {
        display: block !important;
    }

    .details img {
        width: 256px !important;
        height: 256px !important;
    }
    .formation{
        display: none;
    }
}

